import Vue from "vue";
import Vuex from "vuex";
import axios from "axios";
import router from "../router/index";
import Cookies from "js-cookie";
import img from "../../public/no-image.png";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    user: {},
    trainers: [],
    clients: [],
    wrongLogin: false,
    activeClient: {},
    activeTrainer: {},
    types: {},
    values: {},
    dayData: {},
    macros: {}
  },
  getters: {
    getTheToken: (state) => {
      return state.token;
    },
    user: (state) => {
      return state.user
    },
    wrongLogin: (state) => {
      return state.wrongLogin;
    },
    trainers: (state) => {
      return state.trainers
    },
    clients: (state) => {
      return state.clients
    },
    activeClient: (state) => {
      return state.activeClient
    },
    activeTrainer: (state) => {
      return state.activeTrainer
    },
    types: (state) => {
      return state.types
    },
    values: (state) => {
      return state.values
    },
    getDayData: (state) => {
      return state.dayData
    },
    macros: (state) => {
      return state.macros
    }
  },
  mutations: {
    setTokenAndRole(state, payload) {
      Cookies.set("token", payload.token);
      Cookies.set("role", payload.user.role);
    },
    setUserData(state, payload) {
      state.user = { ...payload };
      if (state.user.avatar) {
        state.user.avatar = "data:image/gif;base64," + state.user.avatar;
      } else {
        state.user.avatar = img;
      }
    },
    wrongLogin(state, payload) {
      state.wrongLogin = payload;
    },
    logout(state) {
      Cookies.remove("token");
      Cookies.remove("role")
      Object.assign(state, {
        user: {},
        trainers: [],
        clients: [],
        wrongLogin: false,
        activeClient: {},
        activeTrainer: {},
        types: {},
        values: {},
        dayData: {},
        macros: {}
      });
      router.push({ name: "Login" });
    },
    redirectToApp(state) {
      if (Cookies.get("token") && state.user.role == 3000) {
        router.push({ name: "Admin" });
      } else if (Cookies.get("token") && state.user.role == 3001) {
        router.push({ name: "Trainer" });
      } else if (Cookies.get("token") && state.user.role == 3002) {
        router.push({ name: "Client" });
      }
    },
    setTrainers(state, payload) {
      state.trainers = payload
    },
    setClients(state, payload) {
      state.clients = payload
    },
    activeClient(state, payload) {
      state.activeClient = payload
    },
    activeTrainer(state, payload) {
      state.activeTrainer = payload
    },
    setTypes(state, payload) {
      state.types = payload
    },
    setValues(state, payload) {
      state.values = payload
    },
    setSystemType(state, payload) {
      state.user.systemType = payload
    },
    setCurrency(state, payload) {
      state.user.currency = payload
    },
    setRoleInfo(state, payload) {
      state.user = { ...state.user, ...payload }
    },
    setMacros(state, payload) {
      state.macros = payload
    },
    setExercises(state, payload) {
      if (!payload) {
        state.dayData = {
          exerciseMode: "",
          date: "",
          user: "",
          warmUpExercises: [
            {
              name: Number,
              value: Number,
              option: Number
            }
          ],
          workoutExercises: [
            {
              name: Number,
              value: Number,
              option: Number
            }
          ],
          coolUpExercises: [
            {
              name: Number,
              value: Number,
              option: Number
            }
          ]
        }
      } else {
        state.dayData = payload
      }


    }
  },
  actions: {
    login(context, data) {
      return new Promise(resolve => {
        axios
          .post("/api/login", data, {
            headers: {
              "content-type": "application/json",
            },
          })
          .then(async res => {
            await context.commit("setUserData", res.data.user);
            await context.commit("setTokenAndRole", res.data);
            context.commit("redirectToApp");
            resolve();
          })
          .catch(async (err) => {
            if (
              err.response.data.error.includes("The email or password is wrong")
            ) {
              await context.commit("wrongLogin", true);
            }
            console.log(err);
          });
      });
    },
    getTrainers(context) {
      return new Promise(resolve => {
        axios
          .get("/api/getTrainers", {
            headers: {
              authorization: `Bearer ${Cookies.get("token")}`,
            },
          })
          .then(async res => {
            await context.commit("setTrainers", res.data);
            resolve();
          })
          .catch(async (err) => {
            console.log(err);
          });
      });
    },
    getClients(context) {
      return new Promise(resolve => {
        axios
          .get("/api/getClients", {
            headers: {
              authorization: `Bearer ${Cookies.get("token")}`,
            },
          })
          .then(async res => {
            await context.commit("setClients", res.data);
            resolve();
          })
          .catch(async (err) => {
            console.log(err);
          });
      });
    },
    getUser(context) {
      return new Promise(resolve => {
        axios
          .get("/api/me", {
            headers: {
              authorization: `Bearer ${Cookies.get("token")}`,
            },
          })
          .then(async res => {
            await context.commit("setUserData", res.data);
            resolve();
          })
          .catch(async (err) => {
            console.log(err);
          });
      });
    },
    createClient(context, data) {
      return new Promise(resolve => {
        axios.post("/api/clientSignUp", data, {
          headers: {
            authorization: `Bearer ${Cookies.get("token")}`,
            "content-type": "application/json",
          },
        }).then(() => {
          resolve()
        }).catch((err) => {
          console.log(err)
        })
      })
    },
    createTrainer(context, data) {
      return new Promise(resolve => {
        axios.post("/api/trainerSignUp", data, {
          headers: {
            authorization: `Bearer ${Cookies.get("token")}`,
            "content-type": "application/json",
          },
        }).then(() => {
          resolve()
        }).catch((err) => {
          console.log(err)
        })
      })
    },
    types(context) {
      return new Promise(resolve => {
        axios.get("/api/types", {
          headers: {
            authorization: `Bearer ${Cookies.get("token")}`,
            "content-type": "application/json",
          },
        }).then(async (res) => {
          await context.commit("setTypes", res.data)
          resolve()
        }).catch((err) => {
          console.log(err)
        })
      })
    },
    values(context) {
      return new Promise(resolve => {
        axios.get("/api/values", {
          headers: {
            authorization: `Bearer ${Cookies.get("token")}`,
            "content-type": "application/json",
          },
        }).then(async (res) => {
          await context.commit("setValues", res.data)
          resolve()
        }).catch((err) => {
          console.log(err)
        })
      })
    },
    logout(context) {
      return new Promise(resolve => {
        axios.post("/api/logout", '', {
          headers: {
            authorization: `Bearer ${Cookies.get("token")}`,
            "content-type": "application/json",
          },
        }).then(() => {
          context.commit("logout")
          resolve()
        }).catch((err) => {
          console.log(err)
        })
      })
    },
    getClientsTrainer(context) {
      return new Promise(resolve => {
        axios.get("/api/trainer/getClients", {
          headers: {
            authorization: `Bearer ${Cookies.get("token")}`,
            "content-type": "application/json",
          },
        }).then(async (res) => {
          await context.commit("setClients", res.data)
          resolve()
        }).catch((err) => {
          console.log(err)
        })
      })
    },
    getRoleInfo(context) {
      return new Promise(resolve => {
        axios.get("/api/roleInfo", {
          headers: {
            authorization: `Bearer ${Cookies.get("token")}`,
            "content-type": "application/json",
          },
        }).then(async (res) => {
          await context.commit("setRoleInfo", res.data)
          resolve()
        }).catch((err) => {
          console.log(err)
        })
      })
    },
    updateUser(context, payload) {
      return new Promise(resolve => {
        axios.patch("/api/me", payload, {
          headers: {
            authorization: `Bearer ${Cookies.get("token")}`,
            "content-type": "application/json",
          }
        }).then(async () => {

          resolve()
        }).catch((err) => {
          console.log(err)
        })
      })
    },
    uploadDayData(context, payload) {

      return new Promise(resolve => {
        axios.post(`/api/day`, payload, {
          headers: {
            authorization: `Bearer ${Cookies.get("token")}`,
            "content-type": "application/json",
          }
        }).then(async () => {

          resolve()
        }).catch((err) => {
          console.log(err)
        })
      })
    },
    getMacros(context, payload) {
      return new Promise(resolve => {
        axios.get(`/api/day/macros/${payload}`, {
          headers: {
            authorization: `Bearer ${Cookies.get("token")}`,
            "content-type": "application/json",
          },
        }).then(async (res) => {
          await context.commit("setMacros", res.data)
          resolve()
        }).catch((err) => {
          console.log(err)
        })
      })
    },
    getExercises(context, payload) {
      return new Promise(resolve => {
        axios.get(`/api/day/exercises/${payload.user}/${payload.date}`, {
          headers: {
            authorization: `Bearer ${Cookies.get("token")}`,
            "content-type": "application/json",
          },
        }).then(async (res) => {
          await context.commit("setExercises", res.data)
          resolve()
        }).catch((err) => {
          console.log(err)
        })
      })
    },
    getExercisesClient(context, payload) {
      return new Promise(resolve => {
        axios.get(`/api/dayClient/exercises/${payload}`, {
          headers: {
            authorization: `Bearer ${Cookies.get("token")}`,
            "content-type": "application/json",
          },
        }).then(async (res) => {
          await context.commit("setExercises", res.data)
          resolve()
        }).catch((err) => {
          console.log(err)
        })
      })
    },
    getMacrosClient(context, payload) {
      return new Promise(resolve => {
        axios.get(`/api/dayClient/macros/${payload}`, {
          headers: {
            authorization: `Bearer ${Cookies.get("token")}`,
            "content-type": "application/json",
          },
        }).then(async (res) => {
          await context.commit("setMacros", res.data)
          resolve()
        }).catch((err) => {
          console.log(err)
        })
      })
    },
    uploadDayClient(context, payload) {
      return new Promise(resolve => {
        axios.post(`/api/dayClient`, payload, {
          headers: {
            authorization: `Bearer ${Cookies.get("token")}`,
            "content-type": "application/json",
          },
        }).then(async () => {
          resolve()
        }).catch((err) => {
          console.log(err)
        })
      })
    },

  }
});
